.main-block {

  background-color: $second-text-color;

  &__container {
    display: grid;

    row-gap: 100px;
    padding-block: 100px;

    @include contentPaddingInline;

    @include onTablet {
      row-gap: 140px;
      padding-top: 140px;
      padding-bottom: 156px;
    }

    @include onDesktop {
      padding-top: 138px;
      padding-bottom: 150px;
    }
  }
}
